@import url(https://fonts.googleapis.com/css2?family=Gotu&family=Montserrat:ital,wght@0,400;0,700;1,400&display=swap);
.banner {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	
	align-content: center;
	color: #ffd;
	display: flex;
	flex-direction: column;
	height: 180px;
	justify-content: center;
	text-align: center;
	text-shadow: 1px 1px 1px #a4d555;
}

.banner h1 {
	font-size: 36px;
	font-family: 'Gotu', sans-serif;
	letter-spacing: 1.5px;
	margin-top: 30px;
	text-transform: uppercase;
	text-shadow: 2px 2px 5px rgba(0, 0, 0, .25);
}

.banner p {
	font-size: 14px;
	font-family: 'Montserrat', sans-serif;
	letter-spacing: 1.1px;
}

@media only screen and (min-width: 959.8px) {
	.banner p {
		font-size: 18px;
	}
}
.secao {
	align-items: center;
	position: relative;
}

.secao::after {
	background: rgba(0, 0, 0, .1);
	bottom: 0px;
	box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, );
	content: "";
	height: 1px;
	position: absolute;
	top: bottom;
	width: 100%;
}

.secao img {
	margin-bottom: 60px;
}

.secao h2 {
	font-family: 'Gotu', sans-serif;
	font-size: 24px;
	margin-bottom: 10px;
	text-transform: uppercase;
}

.secao span {
	font-size: 18px;
	margin-bottom: 30px;
	min-height: 55px;
	text-align: center;
}

.secao .btn {
	color: #4cdef5;
	font-family: 'Montserrat';
	font-size: 18px;
	height: 27px;
	padding: 0 20px;
	text-align: center;
	text-shadow: 1px 1px 6px rgba(164, 213, 85, .5);
}
@media only screen and (min-width: 959.8px) {
	.home .content {
		display: flex;
		max-width: 100%;
	}

	.content .secao {
		width: 25%;
	}

	.content .flex-column {
		height: 100vh;
		justify-content: center;
	}

}
.secao h3 {
	font-family: 'Gotu', sans-serif;
	font-size: 24px;
	margin-bottom: 20px;
	text-align: center;
	text-transform: uppercase;
}

.secao p {
	font-size: 18px;
	margin-bottom: 20px;
	text-align: left;
}

@media only screen and (min-width: 747.8px) {
	.sobre .container {
		padding: 60px 20px;
	}

	.sobre > .flex-column {
		flex-direction: row;
		justify-content: space-around;
	}

	.secao h3 {
		font-size: 30px;
	}

	.secao .paragraphs {
		max-width: 50%;
	}

	.secao:nth-of-type(even) .paragraphs {
		order: -1;
	}

	.secao p {
		align-self: baseline;
	}
}




.call {
	background: url(/feliperocha93/refactor-my-site/static/media/bg-call.d2b25d62.jpg) no-repeat center;
	background-size:cover; 
	padding: 10px 20px;
}

.call p {
	color: #fff;
	font-size: 12px;
	font-family: 'Century Gothic';
	font-style: italic;
	text-align: center;
}

.call cite {
	color: #fff;
	font-size: 11px;
}

.header {
	background-color: #a4d555;
}

.header nav {
	background-color: #a4d555;
	height: 100%;
	left: -100%;
	padding-top: 30px;
	position: absolute;
	text-align: center;
	-webkit-transition: left .2s ease-out;
	transition: left .2s ease-out;
	top: 0;
	width: 100%;
	z-index: 1;
}

.header nav.ativo {
	left: 0%;
}

.header button {
	background: none;
	padding: 10px;
	position: absolute;
	right: 0;
	top: 0;
}

.header ul > li {
	border-bottom: 1px solid #ffe;
	font-family: 'Montserrat', sans-serif;
}

.header ul > li:first-child {
	border-top: 1px solid #ffe;
}

.header a {
	color: #ffe;
	display: inline-block;
	font-size: 1.2rem;
	padding: 10px;
}

@media only screen and (min-width: 767.8px) {
	.header {
		height: 77px;
		position: relative;
	}

	.header::after {
		background-color: rgba(0, 0, 0, 0.1);
    bottom: -1px;
    height: 1px;
    content: '';
    position: absolute;
    width: 100%;
	}

	.header nav {
		align-items: baseline;
		display: flex;
		height: auto;
		justify-content: space-around;
		left: 0;
		margin: 0 auto;
		max-width: 1280px;
		padding-top: 0;
		position: relative;
		width: 100%;
	}

	.header button {
		display: none;		
	}

	.header ul > li {
		border: none;
		display: inline-block;
		padding: 0px 10px;
	}

	.header ul > li:first-child {
		border-top: none;
	}
	
}
.social a {
  color: #FFD;
}
.footer {
  background-color: #a4d555;
}

.footer h1 {
  color: #ffd;
  font-size: 18px;
  font-family: 'Gotu', sans-serif;
  margin-bottom: 20px;
  letter-spacing: 1.5px;
  text-align: center;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, .25);
  text-transform: uppercase;
}

.footer h1::after {
  background-color: #ffd;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, .25);
  content: '';
  display: block;
  height: 1px;
  margin: 5px auto 0;
  width: 90%;
}

.footer-bottom {
  font-family: 'Montserrat';
  font-size: 10px;
  letter-spacing: 3px;
  padding: 5px 20px;
  text-align: center;
}

/* 
Name: iRock Reset
Author: Felipe Rocha
Version: 1.0.1
Repository: 'https://github.com/feliperocha93/css-features.git'
References: reset.css by Origamid, Bootstrap 4 reset and my project experience xD
 */

html { 
  font-size: 16px;
}

body { 
  /* MONOSPACE: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace */
  /* SANS-SERIF: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji" */
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1rem;
  line-height: 1.5;
  color: #333;
}

*, *::before, *::after {
	margin: 0;
	padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

a {
	text-decoration: none;
}



button {
  cursor: pointer;
  outline: none;
  font-size: 1rem;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
}

img {
	max-width: 100%;
}

input {
  outline: none;
}

ol, ul {
  list-style: none;
}

.container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1280px;
  padding: 30px 20px;
}

.flex {
  display: flex;
  justify-content: space-evenly;
}

.flex-column {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.hover-border,
.hover-scale {
  -webkit-transition: all ease-in-out .1s;
  transition: all ease-in-out .1s;
}

.hover-border:hover {
  border-bottom: 1px solid;
}

.hover-scale:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.rounded-img {
  border-radius: 200px;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, .35);
	height: 200px;
	width: 200px;
}

.secao:nth-of-type(even){
	background-color: #fee;
}

