@import url('https://fonts.googleapis.com/css2?family=Gotu&family=Montserrat:ital,wght@0,400;0,700;1,400&display=swap');

html { 
  font-size: 16px;
}

body { 
  /* MONOSPACE: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace */
  /* SANS-SERIF: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji" */
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  font-size: 1rem;
  line-height: 1.5;
  color: #333;
}

*, *::before, *::after {
	margin: 0;
	padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

a {
	text-decoration: none;
}



button {
  cursor: pointer;
  outline: none;
  font-size: 1rem;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
}

img {
	max-width: 100%;
}

input {
  outline: none;
}

ol, ul {
  list-style: none;
}

.container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1280px;
  padding: 30px 20px;
}

.flex {
  display: flex;
  justify-content: space-evenly;
}

.flex-column {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.hover-border,
.hover-scale {
  transition: all ease-in-out .1s;
}

.hover-border:hover {
  border-bottom: 1px solid;
}

.hover-scale:hover {
  transform: scale(1.1);
}

.rounded-img {
  border-radius: 200px;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, .35);
	height: 200px;
	width: 200px;
}

.secao:nth-of-type(even){
	background-color: #fee;
}
